<template lang="pug">
#app
  Navigation(:class="{'bgShow': scrollPosition > 100}")
  HeroSec
  PointProduct
  PartnerSec
  TransitPoint
  FAQ
  Footer
</template>

<script>
import Navigation from './components/NaviBar.vue'
import HeroSec from './components/HeroSec.vue'
import HelloWorld from './components/HelloWorld.vue'
import PointProduct from './components/PointProduct.vue'
import PartnerSec from './components/PartnerSec.vue'
import TransitPoint from './components/TransitPoint.vue'
import FAQ from './components/FAQ.vue'
import Footer from './components/Footer.vue'

export default {
  data(){
    return {
      scrollPosition: null,
    }
  },
  name: 'App',
  components: {
    HelloWorld,
    Navigation,
    HeroSec,
    PartnerSec,
    PointProduct,
    TransitPoint,
    FAQ,
    Footer
  },
  methods: {
    updateScroll() {
      this.scrollPosition = window.scrollY
    }
  },
  mounted() {
    window.addEventListener('scroll', this.updateScroll);
  }  
}
</script>

